.ina-modal-inside {
  h2 {
    border-bottom: 1px solid #eee;
    margin: 0;
    padding: 15px 20px;
    font-size: 20px;
  }

  .ina-modal-content {
    padding: 10px 20px;
    font-size: 15px;

    p {
      font-size: 15px;
      margin: 10px 0;
    }
  }

  .ina-modal-actions {
    padding: 0 20px 25px;

    .ina-button {
      background: #2271b1;
      color: #fff;
      text-decoration: none;
      text-shadow: none;
      padding: 10px 20px;
      box-shadow: none;
      border: none;
    }

    .ina-close-without-reloading {
      margin-left: 10px;
      background: #676767;
    }
  }
}

//Debug Bar CSS
.ina-debugger-section {
  position: fixed;
  bottom: 0;
  background: #fff;
  z-index: 99999;
  box-shadow: 0 -7px 7px -9px;
  width: 100%;

  ul {
    list-style: none;
    display: flex;
    justify-content: center;
    margin: 0;

    li {
      padding: 8px;
      margin: 10px 8px;
      font-size: 15px;
    }
  }
}